import React from 'react'

export default function Modal() {

  const closeModal = () => {
    const mo = document.getElementById('modal') 
    mo.style.display = 'none'
  }

  return (
    <div id='modal' style={{display: 'none'}}>
      <div className='modal-container'>
        <span>{'\&times'}</span>
        <p>Modal Text</p>
      </div>
    </div>
  )
}
